import React from "react"
import styled from "styled-components"
import BeerIcon from "../images/cheers.svg"
import SteikIcon from "../images/meat.svg"
import WineIcon from "../images/wine-glass.svg"

const StyledService = styled.div`
  max-width: 1232px;
  padding: 0 1rem;
  margin: 0 auto;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));

  & > div {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin: 1rem 0;
    color: #252525;
    padding: 1rem 0;
  }

  h2 {
    margin: 0;
    margin-top: 0.4rem;
  }
`
const StyledIcon = styled.div`
  width: 50px;
  margin-right: 1rem;

  img {
    width: 100%;
    height: 100%;
  }
`
const Service = () => {
  return (
    <StyledService>
      <div>
        <StyledIcon>
          <img src={BeerIcon} />
        </StyledIcon>
        <div>
          <h2>Лучшее Европейское пиво</h2>
          <p>
            Широкий ассортимент пива из Англии, Шотландии, Германии, Чехии, Бельгии.
          </p>
        </div>
      </div>
      <div>
        <StyledIcon>
          <img src={SteikIcon} />
        </StyledIcon>
        <div>
          <h2>Вкусная кухня по доступной цене</h2>
          <p>
            Сочные стейки и бургеры. Также шеф повар готовит настоящую итальянскую пиццу.
          </p>
        </div>
      </div>
      <div>
        <StyledIcon>
          <img src={WineIcon} />
        </StyledIcon>
        <div>
          <h2>Всегда свежее пиво</h2>
          <p>
            Уникальная холодильная камера хранит пиво при температуре 3 градуса, что позволяет насладиться настоящим и прохладным пивом          </p>
        </div>
      </div>
    </StyledService>
  )
}

export default Service
